"use strict";

window.addEventListener("DOMContentLoaded", () => {
    const burgerBtn = document.querySelector(".burger");
    const menuNav = document.querySelector(".navside");
    const closeNav = document.querySelector(".navClose");

    burgerBtn.addEventListener("click", () => {
        menuNav.classList.add("activeMenu");
    });

    closeNav.addEventListener("click", () => {
        menuNav.classList.remove("activeMenu");
    });

    document.addEventListener("mouseup", (e) => {
        if (e.target !== closeNav && e.target !== menuNav) {
            menuNav.classList.remove("activeMenu");
        }
    });

    const navLists = document.querySelector(".links");
    const navLinks = document.querySelectorAll(".link");

    navLinks.forEach((item) => {
        item.addEventListener("click", () => {
            navLists.querySelector(".link-active").classList.remove("link-active");

            item.classList.add("link-active");
        });
    });

    const year = document.querySelector(".year");
    year.textContent = new Date().getFullYear();
});
